@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");

body {
  background-color: #f82a6b;
  box-sizing: border-box;
  /* height: 100%; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.upload-btn {
  position: fixed;
  bottom: 30px;
  right: 30px;
}

.upload-btn span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f72585;
  color: #fff;
  font-size: 35px;
  border: 3px solid #fff;
  transition: opacity 0.3s ease-in-out;
  cursor: pointer;
  font-weight: bold;
}

.upload-btn input {
  display: none;
}

.upload-btn span:active {
  opacity: 0.5;
}

.preview-img {
  height: 255px;
  width: 340px;
  border: 3px solid #fff;
  border-radius: 6px;
  margin: 5px;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
}

.image-container {
  width: 100%;
  height: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.colors {
  display: flex;
  justify-content: center;
  align-items: center;
}

.color-box {
  width: 70px;
  height: 70px;
  border-radius: 6px;
  margin: 3px;
  cursor: pointer;
  border: 2px solid #fff;
  box-shadow: 0px 1px 4px rgba(14, 31, 53, 0.12),
    0px 4px 8px rgba(14, 31, 53, 0.1), 0px 6px 12px rgba(14, 31, 53, 0.08);
}

.title {
  font-family: "Lobster", cursive;
  color: #fff;
  position: absolute;
  font-size: 150px;
  z-index: -1;
}

.swal2-image {
  border: 3px solid #fff;
  border-radius: 6px;
  box-shadow: 0px 1px 4px rgba(14, 31, 53, 0.12),
    0px 4px 8px rgba(14, 31, 53, 0.1), 0px 6px 12px rgba(14, 31, 53, 0.08);
}
